@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page{background:#000;width:100%;height:100vh;caret-color:transparent}.title{font-weight:bold;font-size:52px}
.avatar{position:relative}.avatar__wrapper{width:220px;height:220px;border:8px solid #fff;border-radius:160px;background:#edf2f4;position:relative;overflow:hidden}.avatar__wrapper_small{width:32px;height:32px;border:4px solid #fff;border-radius:50%;background:#edf2f4;position:relative;overflow:hidden}.avatar--outined{border:8px solid #d90429}.avatar__refresh{width:60px;height:60px;background:#fff;position:absolute;top:180px;left:180px;border-radius:50%;border:none;cursor:pointer;display:flex;justify-content:center;align-items:center}.avatar__face{width:100%}
.button{height:56px;border-radius:20px;padding:0 72px;font-size:24px;background:#d90429;border:none;font-weight:bold;cursor:pointer}.button_disabled{pointer-events:none;background:#8d99ae}.button:hover{background:#ee0830;box-shadow:3px 4px 10px rgba(217,4,41,.36)}
.text{color:#fff;font-family:"Montserrat",sans-serif}
.home{display:flex;flex-direction:column;align-items:center}.home__container{max-width:717px;width:100%;display:flex;flex-direction:column;align-items:center;height:100%;justify-content:space-around}.home__avatar_container{width:100%;display:flex;flex-wrap:wrap;justify-content:space-around;align-items:center;margin-bottom:150px}.home__pseudo{height:48px;border-radius:5px;font-size:18px;font-weight:500;text-align:center;caret-color:#000}.home__pseudo_container{height:220px;display:flex;flex-direction:column;justify-content:space-between}@media(max-width: 541px){.avatar_container{align-items:center}.pseudo_container{margin-bottom:0px}}
.lobby{display:flex;justify-content:center}.lobby__container{max-width:590px;width:100%;padding:0 40px;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.lobby__invite{height:83px;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.lobby__list{height:280px;max-width:400px;width:100%;border-top:#fff 1px solid}.lobby__item{height:54px;border-bottom:#fff 1px solid;display:flex;align-items:center;font-size:20px;padding:0 12px}.lobby__spacing{width:22px}.lobby__svg{height:17px;fill:#fff;margin-left:auto}.lobby__svg_clickable{cursor:pointer}.lobby__start_message{font-weight:bold;font-size:28px}.lobby__start_container{width:100%;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;grid-gap:12px;gap:12px}@media(max-width: 636px){.lobby_item_mobile{justify-content:space-around}}
